.project {
  width: 715px;
  height: 415px;
  color: black;

  background-color: rgba(255, 255, 255, 0.75);
  border-radius: 15px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  padding: 20px;
  box-shadow: 0px 0px 20px white;
  margin-top: 30px;
  margin-bottom: 30px;
  font-family: "SF";
}

.project-info {
  height: 95%;
  width: 50%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.project-info p {
  text-align: center;
  line-height: 1.4;
  font-family: "SF";
}

.project-info a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  font-style: italic;
  font-family: "SF";
}

.project img {
  min-width: 30%;
  max-width: 200px;

  border-radius: 15px;

  box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset,
    rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

.project-links a img {
  width: 50px;
  box-shadow: none;
}

.project-info a {
  overflow: visible;
}

.project-links {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

@media (max-width: 850px) {
  .project {
    flex-direction: column;
  }

  .project-info p {
    display: none;
  }

  .project-info {
    font-family: "SF";
    width: 90%;
  }
}
